import { Box, Button, HStack, Image, Text, VStack } from '@chakra-ui/react';
import styled from '@emotion/styled';
import { useEffect, useState } from 'react';
import base64 from 'base-64';

import { fetchInstance, getBaseUrl } from '@/api/instance';

interface WishlistItem {
  wishId: number;
  productId: number;
  productName: string;
  productPrice: number;
  productImageUrl: string;
}

interface WishlistResponseData {
  totalElements: number;
  totalPages: number;
  first: boolean;
  last: boolean;
  size: number;
  content: WishlistItem[];
  number: number;
  sort: {
    empty: boolean;
    sorted: boolean;
    unsorted: boolean;
  };
  numberOfElements: number;
  pageable: {
    pageNumber: number;
    pageSize: number;
    sort: {
      empty: boolean;
      sorted: boolean;
      unsorted: boolean;
    };
    offset: number;
    paged: boolean;
    unpaged: boolean;
  };
  empty: boolean;
}

export const MyAccountPage = () => {
  const authInfo = localStorage.getItem('authToken');
  const [wishlist, setWishlist] = useState<WishlistItem[]>([]);
  const [page, setPage] = useState(0);
  const [totalPages, setTotalPages] = useState(0);
  const [userName, setUserName] = useState<string>("");

  useEffect(() => {
    if (authInfo) {
      try {
        const [headerEncoded, payloadEncoded, signature] = authInfo.split('.');
        const payload = JSON.parse(base64.decode(payloadEncoded));
  
        console.log('Token Header:', JSON.parse(base64.decode(headerEncoded)));
        console.log('Token Payload:', payload);
        console.log('Token Signature:', signature);
        console.log('Payload Keys:', Object.keys(payload));
  
        if (payload.sub) {
          const email = payload.sub;
          const displayName = email.split('@')[0];
          setUserName(displayName);
        }
      } catch (error) {
        console.error('  :', error);
      }
    } else {
      setUserName("");
    }
  }, [authInfo]);

  const getWishlistPath = () => `${getBaseUrl()}/api/wishes`;

  const fetchWishlist = async (page: number) => {
    try {
      const response = await fetchInstance.get<WishlistResponseData>(
        `${getWishlistPath()}?page=${page}&size=10&sort=createdDate,desc`,
        {
          headers: {
            Authorization: `Bearer ${authInfo}`,
          },
          withCredentials: true,
        }
      );

      if (response.status === 200) {
        const data = response.data;
        console.log('Response data:', data);
        setWishlist(data.content);
        setTotalPages(data.totalPages);
      } else if (response.status === 403) {
        alert('  .  .');
      } else {
        alert('   .');
      }
    } catch (error) {
      console.error('   :', error);
      alert('    .');
    }
  };

  useEffect(() => {
    fetchWishlist(page);
  }, [page, authInfo]);

  const handleDelete = async (wishId: number) => {
    try {
      const response = await fetch(`${getBaseUrl()}/api/wishes/${wishId}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${authInfo}`,
        },
      });

      if (response.ok) {
        setWishlist((prev) => prev.filter((item) => item.wishId !== wishId));
        alert('  .');
      } else if (response.status === 400) {
        alert('    .');
      } else if (response.status === 403) {
        alert('  .  .');
      } else {
        alert('   .');
      }
    } catch (error) {
      console.error('   :', error);
      alert('    .');
    }
  };

  const handleLogout = () => {
    localStorage.removeItem('authToken');
    const redirectURL = `${window.location.origin}`;
    window.location.replace(redirectURL);
  };

  return (
    <Wrapper>
      <Text fontSize="2xl" fontWeight="bold">
        {authInfo ? userName : " "} !
      </Text>
      <Box height="64px" />
      <VStack spacing={4} align="stretch">
        {wishlist.length > 0 ? (
          wishlist.map((item) => (
            <Box key={item.wishId} p={4} borderWidth="1px" borderRadius="lg">
              <HStack spacing={4}>
                <Image boxSize="50px" src={item.productImageUrl} alt={item.productName} />
                <VStack align="start">
                  <Text fontSize="lg" fontWeight="bold">{item.productName}</Text>
                  <Text>{item.productPrice}</Text>
                </VStack>
                <Button
                  colorScheme="red"
                  size="sm"
                  onClick={() => handleDelete(item.wishId)}
                >
                  
                </Button>
              </HStack>
            </Box>
          ))
        ) : (
          <Text>  .</Text>
        )}
      </VStack>
      <HStack spacing={4} mt={4}>
        <Button
          onClick={() => setPage((prev) => Math.max(prev - 1, 0))}
          isDisabled={page === 0 || totalPages === 0}
        >
          
        </Button>
        <Text>{totalPages === 0 ? 1 : page + 1} / {totalPages === 0 ? 1 : totalPages}</Text>
        <Button
          onClick={() => setPage((prev) => Math.min(prev + 1, totalPages - 1))}
          isDisabled={page === totalPages - 1 || totalPages === 0}
        >
          
        </Button>
      </HStack>
      <Box height="64px" />
      <Button colorScheme="gray" size="sm" onClick={handleLogout}>
        
      </Button>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  width: 100%;
  padding: 80px 0 120px;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  font-size: 36px;
`;